@import "Root";

.center {
  display: flex;
  justify-content: center;
}

#photoProfile {
  display: block;
  justify-content: center;
  text-align: center;
  margin-top: 1rem;
}

.header {
  font-family: $font-header;
  color: $color-black;
}

#photoProfile .header {
  margin-top: 0.3rem;
  font-size: 33px;
}

#divider {
  border-bottom: solid 4px $color-black;
  border-radius: 5px;
  width: 80%;
  margin: auto;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

#sidebar {
  background-color: $color-light-gray;
}

#sidebar .item {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

#sidebar .primary {
  font-family: $font-sub;
  font-size: 23px !important;
  color: $color-black;
}

#sidebar .text {
  margin-top: 2px;
}

#sidebar .secondary {
  font-family: $font-sub;
  font-size: 20px !important;
  color: $color-black;
}

.link {
  text-decoration: none;
  color: black !important;
}

.link:hover {
  text-decoration: none;
  color: black !important;
}

.link:visited {
  color: black !important;
}
